import React, { useState, useEffect } from 'react';
import '../styles/ProFile.css';

function Profile({ onBack, onSaveSuccess }) {
    const initialState = {
        id: '',
        currentPassword: '',
        password: '',
        confirmPassword: '',
        name: '',
        email: '',
        addr1: '',
        addr2: '',
        addr3: '',
        phone1: '',
        phone2: '',
        phone3: '',
        marketing: ''
    };

    const [formData, setFormData] = useState(initialState);
    const [dbPassword, setDbPassword] = useState('');
    const [showModal, setShowModal] = useState(false);
    const [isConfirmed, setIsConfirmed] = useState(false);

    useEffect(() => {
        const loginData = localStorage.getItem('loginUser');
        if (loginData) {
            const currentUser = JSON.parse(loginData);
            //  
            const members = JSON.parse(localStorage.getItem('')) || [];

            //   
            const user = members.find(t => t.id === currentUser.id);
            setDbPassword(user.password || '');

            const phoneParts = user.phone ? user.phone.split('-') : ['', '', ''];

            setFormData({
                ...initialState,
                id: user.id || '',
                name: user.name || '',
                email: user.email || '',
                addr1: user.address?.addr1 || '',
                addr2: user.address?.addr2 || '',
                addr3: user.address?.addr3 || '',
                phone1: phoneParts[0] || '',
                phone2: phoneParts[1] || '',
                phone3: phoneParts[2] || '',
                marketing: user.marketing || '',
                favorites: user.favorites || [],
                cart: user.cart || [],
                orders: user.orders || [],
            });
        }
    }, []);

    const handleChange = (e) => {
        const { name, value } = e.target;
        if (name === 'id') return;

        const noSpaceNames = ['currentPassword', 'password', 'confirmPassword'];
        const finalValue = noSpaceNames.includes(name) ? value.replace(/\s/g, '') : value;

        setFormData(prev => ({ ...prev, [name]: finalValue }));
    };

    const validateFields = () => {
        if (formData.currentPassword !== dbPassword) {
            alert('   .');
            return false;
        }

        if (formData.password.length < 8 || formData.password.length > 20) {
            alert(' 8  20  . ( )');
            return false;
        }

        if (formData.password !== formData.confirmPassword) {
            alert('  .');
            return false;
        }

        return true;
    };

    const handleSave = () => {
        if (!validateFields()) return;

        const updatedUser = {
            id: formData.id,
            password: formData.password,
            name: formData.name,
            email: formData.email,
            address: {
                addr1: formData.addr1,
                addr2: formData.addr2,
                addr3: formData.addr3,
                zipcode: formData.addr1
            },
            phone: `${formData.phone1}-${formData.phone2}-${formData.phone3}`,
            marketing: formData.marketing,
            favorites: formData.favorites,
            cart: formData.cart,
            orders: formData.orders
        };

        localStorage.setItem('loginUser', JSON.stringify(updatedUser));

        const allUsers = JSON.parse(localStorage.getItem('') || '[]');
        const updatedList = allUsers.map(u => u.id === formData.id ? updatedUser : u);
        localStorage.setItem('', JSON.stringify(updatedList));

        alert('  .');

        if (onSaveSuccess) {
            onSaveSuccess();
        } else if (onBack) {
            onBack();
        } else {
            window.location.href = "/mypage";
        }
    };

    const handleReset = () => {
        if (window.confirm(" ?  .")) {
            setFormData(prev => ({
                ...initialState,
                id: prev.id
            }));
        }
    };

    // []       
    const handleCancel = () => {
        if (onBack) {
            onBack(); //      
        } else {
            window.location.href = "/mypage"; //   
        }
    };

    const handleLeaveSubmit = () => {
        if (!isConfirmed) {
            alert("    .");
            return;
        }
        if (window.confirm(" ?")) {
            const currentId = formData.id;
            const allUsers = JSON.parse(localStorage.getItem('') || '[]');
            const remainingUsers = allUsers.filter(user => user.id !== currentId);
            localStorage.setItem('', JSON.stringify(remainingUsers));
            localStorage.removeItem('loginUser');
            alert(" .");
            window.location.href = "/";
        }
    };

    console.log(formData);

    return (
        <div className="profile-edit-container">
            <div className="edit-header-box">
                <div className="header-top">
                    <h3 className="header-title">  ?</h3>
                </div>
                <p className="header-subtitle"> (*)     .</p>

                <table className="edit-table">
                    <tbody>
                        <tr>
                            <th>*</th>
                            <td>
                                <input
                                    type="text"
                                    name="id"
                                    value={formData.id}
                                    readOnly
                                    tabIndex="-1"
                                    className="read-only-input"
                                    style={{ backgroundColor: '#f5f5f5', cursor: 'not-allowed' }}
                                />
                            </td>
                        </tr>
                        <tr>
                            <th> *</th>
                            <td>
                                <input
                                    type="password"
                                    name="currentPassword"
                                    value={formData.currentPassword}
                                    onChange={handleChange}
                                    placeholder="  "
                                />
                            </td>
                        </tr>
                        <tr>
                            <th>*</th>
                            <td>
                                <input
                                    type="password"
                                    name="password"
                                    value={formData.password}
                                    onChange={handleChange}
                                    placeholder="8~20 ,, "
                                />
                            </td>
                        </tr>
                        <tr>
                            <th> *</th>
                            <td>
                                <input
                                    type="password"
                                    name="confirmPassword"
                                    value={formData.confirmPassword}
                                    onChange={handleChange}
                                    placeholder="    "
                                />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td><input type="text" name="name" value={formData.name} onChange={handleChange} /></td>
                        </tr>
                        <tr>
                            <th></th>
                            <td><input type="email" name="email" value={formData.email} onChange={handleChange} className="email-input" /></td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <div className="address-group">
                                    <div className="address-row-top">
                                        <input type="text" name="addr1" value={formData.addr1} onChange={handleChange} className="postcode-input" placeholder="" />
                                        <button type="button" className="post-btn"></button>
                                    </div>
                                    <input type="text" name="addr2" value={formData.addr2} onChange={handleChange} className="address-full" placeholder="" />
                                    <input type="text" name="addr3" value={formData.addr3} onChange={handleChange} className="address-full" placeholder="" />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td className="phone-td">
                                <div className="phone-wrapper">
                                    <input type="text" name="phone1" value={formData.phone1} onChange={handleChange} />
                                    <span>-</span>
                                    <input type="text" name="phone2" value={formData.phone2} onChange={handleChange} />
                                    <span>-</span>
                                    <input type="text" name="phone3" value={formData.phone3} onChange={handleChange} />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td>
                                <div className="marketing-flex">
                                    <label className="radio-item">
                                        <input type="radio" name="marketing" value="" checked={formData.marketing === ''} onChange={handleChange} />
                                        <span></span>
                                    </label>
                                    <label className="radio-item">
                                        <input type="radio" name="marketing" value="" checked={formData.marketing === ''} onChange={handleChange} />
                                        <span></span>
                                    </label>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div className="main-button-group">
                <button className="btn-save" onClick={handleSave}></button>
                <button className="btn-reset" onClick={handleReset}></button>
                {/* [] handleCancel   */}
                <button className="btn-cancel" onClick={handleCancel}></button>
                <button className="btn-leave" onClick={() => setShowModal(true)}></button>
            </div>

            {showModal && (
                <div className="modal-overlay">
                    <div className="modal-content">
                        <h4>  </h4>
                        <p>  ?</p>
                        <div className="modal-check-area">
                            <input type="checkbox" id="leave-check" checked={isConfirmed} onChange={(e) => setIsConfirmed(e.target.checked)} />
                            <label htmlFor="leave-check">     </label>
                        </div>
                        <div className="modal-btns">
                            <button className="confirm-leave-btn" onClick={handleLeaveSubmit}></button>
                            <button className="close-modal-btn" onClick={() => setShowModal(false)}></button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Profile;