import '../../styles/Header.css';
import { useNavigate } from 'react-router';

function Header({ loginUser, setLoginUser }) {
    const navigate = useNavigate();
    const logoPath = "/images/logo_petLab.png";

    const handleLogout = () => {
        localStorage.removeItem("loginUser");
        setLoginUser(null); //   
        navigate("/login");
    };

    return (
        <header className="header-container">
            <div className="header-logo">
                <img src={logoPath} alt=" " className="logo-img" onClick={() => navigate('/')} />
            </div>

            <nav className="header-right">
                <span className="header-menu" onClick={() => navigate('/funding')}></span>
                <span className="header-menu" onClick={() => navigate('/mypage')}></span>

                {loginUser ? (
                    <>
                        <span className="header-menu">{loginUser.id} </span>
                        <span className="header-menu logout" onClick={handleLogout}></span>
                    </>
                ) : (
                    <>
                        <span className="header-menu" onClick={() => navigate('/login')}></span>
                        <span className="header-menu" onClick={() => navigate('/register')}></span>
                    </>
                )}
            </nav>
        </header>
    );
}

export default Header;
