import { useState } from "react";
import { useNavigate, useOutletContext } from "react-router-dom";
import { Container, Card, Form, Button, Stack } from "react-bootstrap";


export default function PostWritePage() {
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [category, setCategory] = useState("free");
    const navigate = useNavigate();

    const { createPost } = useOutletContext();


    const submit = () => {
        const success = createPost({ title, content, category });
        if (!success) return;
        navigate(`../board/${category}`);
    };


    return (
        <Container className="community-container">
            <h2 className="community-title"> </h2>
            <Card className="community-card">
                <Form>
                    <Form.Select
                        className="mb-2"
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                    >
                        <option value="free"></option>
                        <option value="info"></option>
                        <option value="qna"> / </option>
                        <option value="feedback"></option>
                    </Form.Select> 

                    <Form.Control
                        className="mb-2"
                        placeholder=""
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                    />
                    <Form.Control
                        as="textarea"
                        rows={6}
                        placeholder=""
                        value={content}
                        onChange={(e) => setContent(e.target.value)}
                    />
                    <Stack direction="horizontal" gap={2} className="mt-3">
                        <Button variant="secondary" onClick={() => navigate(`../board/${category}`)}></Button>
                        <Button onClick={submit}></Button>
                    </Stack>
                </Form>
            </Card>
        </Container>
    );
}