import { Outlet, useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import "../../styles/Community.css";

export default function CommunityMain() {
    const { fundingId } = useParams();

    const [funding, setFunding] = useState(null);

    useEffect(() => {
        if (!fundingId) return;

        const data = localStorage.getItem("fundingList");
        if (!data) return;

        const fundings = JSON.parse(data);
        const found = fundings.find(f => f.id === Number(fundingId));
        setFunding(found || null);
    }, [fundingId]);

    
    const [loginUser, setLoginUser] = useState(() => {
        const saved = localStorage.getItem("loginUser");
        return saved ? JSON.parse(saved) : null;
    }); //  

    const [posts, setPosts] = useState(() => {
        const saved = localStorage.getItem(" ");
        return saved ? JSON.parse(saved) : [];
    });

    /* =========================
          
    ========================= */
    const deletePost = (postId) => {
        setPosts(prev => prev.filter(p => p.id !== postId));
    };

    /* =========================
          
    ========================= */
    const updatePost = (postId, updatedData) => {
        if (!updatedData.title?.trim()) {
            alert(" .");
            return false;
        }

        if (!updatedData.content?.trim()) {
            alert(" .");
            return false;
        }

        setPosts(prev =>
            prev.map(p =>
                p.id === postId ? { ...p, ...updatedData } : p
            )
        );

        return true;
    };


    /* =========================
          
    ========================= */
    const deleteComment = (postId, commentId) => {
        setPosts(prev =>
            prev.map(p =>
                p.id === postId
                    ? {
                        ...p,
                        comments: p.comments.filter(c => c.id !== commentId)
                    }
                    : p
            )
        );
    };

    useEffect(() => {
        localStorage.setItem(" ", JSON.stringify(posts));
    }, [posts]);

    const filteredPosts = fundingId
        ? posts.filter(p => p.fundingId === Number(fundingId))
        : posts;

    const ALLOWED_CATEGORIES = ["free", "info", "qna", "feedback"];

    const createPost = ({ title, content, category }) => {
        if (!loginUser) return false;

        if (!title || !title.trim()) {
            alert(" .");
            return false;
        }

        if (!content || !content.trim()) {
            alert(" .");
            return false;
        }

        if (!ALLOWED_CATEGORIES.includes(category)) {
            alert("  .");
            return false;
        }

        if (title.length > 100) {
            alert(" 100  .");
            return false;
        }

        if (content.length > 2000) {
            alert(" 2000  .");
            return false;
        }

        const newPost = {
            id: Date.now(),
            fundingId: Number(fundingId),
            title: title.trim(),
            content: content.trim(),
            category,
            author: loginUser.id,
            views: 0,
            comments: [],
            date: new Date().toLocaleDateString(),
        };

        setPosts(prev => [newPost, ...prev]);
        return true;   //  
    };


    const addComment = (postId, text) => {
        if (!text || !text.trim()) {
            alert(" .");
            return;
        }

        setPosts((prev) =>
            prev.map((p) => {
                if (p.id !== postId) return p;

                return {
                    ...p,
                    comments: [
                        ...p.comments,
                        {
                            id: Date.now(),
                            author: loginUser.id,
                            text: text.trim(),
                            date: new Date().toLocaleString(),
                        },
                    ],
                };
            })
        );
    };


    const increaseView = (postId) => {
        setPosts(prev =>
            prev.map(p =>
                p.id === postId ? { ...p, views: p.views + 1 } : p
            )
        );
    };

    return (
        <div className="community-page">
            <Outlet
                context={{
                    posts: filteredPosts,
                    funding,
                    createPost,
                    addComment,
                    increaseView,
                    deletePost,
                    updatePost,
                    deleteComment,
                }}
            />
        </div>
    );
}
