import { useParams, useNavigate, useOutletContext, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { Container, Card, Form, Button, Stack } from "react-bootstrap";
import FundingHeader from "../../components/common/FundingHeader";
import "../../styles/Post.css";


export default function PostDetailPage() {
    const { id } = useParams();
    const navigate = useNavigate();
    const location = useLocation();
    const category = location.state?.category ?? "free";
    const {
        posts = [],
        addComment,
        increaseView,
        funding,
        deletePost,
        deleteComment
    } = useOutletContext();
    const post = posts.find((p) => p.id === Number(id));
    const [comment, setComment] = useState("");

    const loginUser = JSON.parse(localStorage.getItem("loginUser"));
    const isLogin = !!loginUser; //   

    //    ===   
    const isAuthor = isLogin && loginUser.id === post?.author;

    const handleEdit = () => {
        navigate(`../board/${category}/edit/${post.id}`, {
            state: { post }
        });
    };

    const handleDelete = () => {
        if (!window.confirm(" ?")) return;
        deletePost(post.id);
        alert(" .");
        navigate(`../board/${category}`);
    };

    useEffect(() => {
        if (post) increaseView(post.id);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);


    if (!post) {
        return (
            <Container className="mt-4 text-center">
                <p>   .</p>
                <Button onClick={() => navigate("../board/free")}>
                    
                </Button>
            </Container>
        );
    }


    return (
        <Container className="community-container">
            {funding && <FundingHeader funding={funding} />}
            <Card className="community-card">
                <div className="detail-header">
                    <strong>{post.title}</strong>
                    <span> {post.views}</span>

                    {isAuthor && (
                        <div className="post-action-buttons">
                            <Button
                                size="sm"
                                variant="outline-primary"
                                onClick={handleEdit}
                            >
                                
                            </Button>
                            <Button
                                size="sm"
                                variant="outline-danger"
                                onClick={handleDelete}
                            >
                                
                            </Button>
                        </div>
                    )}
                </div>
                <div className="detail-content">{post.content}</div>


                <div className="comment-section">
                    {post.comments.length === 0 ? (
                    <p className="text-muted">  .</p>
                ) : (
                    post.comments.map((c) => {
                        const isCommentAuthor =
                            isLogin && loginUser.id === c.author;

                        return (
                            <div key={c.id} className="comment">
                                <div className="comment-header">
                                    <div className="comment-info">
                                        <strong className="comment-author">{c.author}</strong>
                                        <span className="comment-date">{c.date}</span>
                                    </div>

                                    {/*      */}
                                    {isCommentAuthor && (
                                        <Button
                                            size="sm"
                                            variant="link"
                                            className="comment-delete"
                                            onClick={() => {
                                                if (!window.confirm(" ?")) return;
                                                deleteComment(post.id, c.id);
                                            }}
                                        >
                                            
                                        </Button>
                                    )}
                                </div>

                                <div className="comment-body">{c.text}</div>
                            </div>
                        );
                    })
                )}

                    <Form.Control
                        className="mt-2"
                        placeholder={
                            isLogin ? " " : "    "
                        }
                        value={comment}
                        disabled={!isLogin}
                        onChange={(e) => setComment(e.target.value)}
                    />
                    <Stack direction="horizontal" gap={2} className="mt-2" style={{justifyContent : "space-between"}}>
                        <Button
                            size="sm"
                            onClick={() => {
                            if (!comment) return;
                            addComment(post.id, comment);
                            setComment("");
                            }}
                        ></Button>
                        <Button size="sm" variant="outline-secondary" onClick={() => navigate(`../board/${category}`)}></Button>
                    </Stack>
                </div>
            </Card>
        </Container>
    );
}